* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

input {
  width: 300px;
  height: 50px;
  padding: 0 10px 0 10px;}

    button {
      width: 100px;
      height: 50px;
      margin-left: 20px;
      cursor: pointer;
  }

  .timer{
    display: flex;
  }

.value {
  font-weight: bold;
  font-size: 32px;
}

.label{
  font-size: 20px;
}
.field {
padding: 10px 0 ;
margin-right: 10px;
}

.field:last-child {
  margin-right: 0;
}
  